/* -------------------------------------------------------------------------

		util.sass
		
		案件に依存せず汎用的に使うClassを定義
		
------------------------------------------------------------------------- */
/* float clear
------------------------------------------------- */
/*for modern browser*/
.clear_fix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
}

/*for IE 5.5-7*/
.clear_fix {
  zoom: 1;
}

.clear_both {
  clear: both;
  height: 1px;
  font-size: 1px;
  text-align: center;
  line-height: 0;
}

.clear {
  clear: both;
}

/* float
------------------------------------------------- */
.float_r {
  float: right;
  display: inline;
}

.float_l {
  float: left;
  display: inline;
}

/* text align
-------------------------------------------------- */
.text_center {
  text-align: center !important;
}

.text_left {
  text-align: left !important;
}

.text_right {
  text-align: right !important;
}

/* font family
----------------------------------------------------- */
/* Text underline */
.underline {
  border-bottom: 1px solid #999;
}

/* visibility of element */
.show_sm {
  display: none !important;
  visibility: hidden !important;
}

/* グリッドレイアウト用mixin (IE8以上)
usage:
■12分割で間隔が20pxのグリッドのクラスを作る場合
・SASS
@include grid_system(12, 20px);
・HTML
<div class="grid_system_12">  ← このdivへの幅指定はNG
	<div class="grid_6"></div> ┐
	<div class="grid_3"></div> ├ 子要素は合計が12になるようにクラス名を付ける
	<div class="grid_3"></div> ┘
</div>
------------------------------------------------- */
/* 
 * PAGE : Main visaul
 * SECTION : Header navigation
 */
html {
  background: none;
  background-image: none;
}

body {
  position: relative;
  background: #fff;
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  letter-spacing: 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

body.mobile_nav_open {
  overflow: show;
}

/* -------------------------------------------------------------------------
		print.scss
------------------------------------------------------------------------- */
@media print {
  /* IE zoom
	------------------------------------------------- */
  /* for IE6 or older */
  * html body {
    zoom: 0.7;
  }
  /* for IE7 */
  * + html body {
    zoom: 1;
  }
}
